          "p",
          null,
          "Twitter launched 9 months before SXSW in 2007 and was seeing decent amount of traction, several thousand users. Many of its users were going to SXSW, so it went too. Ev Williams says they created a Twitter visualizer and negotiated with the festival to put flat panel screens in the hallways. They paid $11K for this and set up the TVs (those were the only money Twitter ever spent on marketing). They created an event-specific feature where you could text \"join sxsw\" to 40404 and you would show up on screens and if you were not a Twitter user already, you would automatically be following ~6 \"ambassadors\" who were already on Twitter and attended SXSW - they advertised this on the screens in the hallways. By the end of the conference, Twitter jumped from 12k tweets a day to 60k and also won the SXSW Web Award leading to press coverage and more users."
