function tr(word) {
  let words = {
    // translations for Navigation
    'Boris Burkov': {en: 'Boris Burkov', ru: ' '},
    'About me': {en: 'About me', ru: ' '},
    'All': {en: 'All', ru: ''},

    // translation for categories
    'how-life-works': {en: 'How life works', ru: '  '},
    'programming': {en: 'Programming', ru: ''},
    'business': {en: 'Business', ru: ''},
    'economy': {en: 'Economy', ru: ''},
    'biomed': {en: 'Biology and medicine', ru: '  '},
    'math': {en: 'Mathematics', ru: ''},
    'music': {en: 'Music', ru: ''},
    'history': {en: 'History', ru: ''},
    'people': {en: 'People', ru: ''},

    // translations for Blog
    'Time to read': {en: 'Time to read', ru: '  '},
    'Continue': {en: 'Continue', ru: ''},

    // translations for Post
    'Author': {en: 'Author', ru: ''},
    'Date': {en: 'Date', ru: ''},
    'Categories': {en: 'Categories', ru: ''},
    'Previous post': {en: 'Previous post', ru: ' '},
    'Next post': {en: 'Next post', ru: ' '},
    'All posts': {en: 'All posts', ru: ' '}
  };

  // localStorage might not be available, if it's server side
  let language;
  if (typeof localStorage !== 'undefined') language = localStorage.getItem('language');
  else language = 'en';

  if (Object.keys(words).indexOf(word) !== -1) {
    if (language === 'en') return words[word]['en'];
    else if (language === 'ru') return words[word]['ru'];
  } else {
    console.log(`No word ${word} in translations list`);
  }
}

export default tr



