import React from 'react';
import { Link, Location } from 'react-router-dom';
import tr from 'services/translate.jsx';

require("pages/Blog.scss");


class Blog extends React.Component {
  render() {
    return (
      <div className="row">
        <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12 blog-2-col">
          { Object.values(this.props.posts).sort((a, b) => { return b.metadata.id.localeCompare(a.metadata.id) }).filter((el, i) => { return i % 2 === 0; }).map((post) =>
            <div key={post.metadata.id} className="hpanel blog-box">
              <div className="panel-heading">
                <div className="media clearfix">
                  <a className="pull-left">
                    <img src={ post.metadata.authors_avatar } alt="profile-picture" />
                  </a>
                  <div className="media-body">
                    <span className="post-authorship">
                      <small><span className="font-bold">{ tr(post.metadata.author) }</span> </small>
                      <br />
                      <small className="text-muted"><i className="fa fa-calendar"> </i> { post.metadata.date_created }</small>
                    </span>
                    <span className="pull-right">
                      <small><i className="fa fa-clock-o"> </i> {tr('Time to read')}: { post.metadata.time_to_read } min</small>
                      <br />
                      <small>{ post.metadata.categories.map((category, index) =>
                        <span key={category}>
                          <span>{ !!index && ' ' }</span>
                          <span className={`label label-${category}`}>{ tr(category) }</span>
                        </span>
                      )}
                      </small>
                      {/*<small><i className="fa fa-eye"> </i> : 62 </small>*/}
                      {/*<br />*/}
                      {/*<small><i className="fa fa-comments-o"> </i> : 10</small>*/}
                    </span>
                  </div>
                </div>
              </div>
              <div className="panel-image">
                <img className="img-responsive center-block" src={ post.metadata.cover } alt="" />
                <div className="title">
                  <Link to={`/blog/${post.metadata.id}`}> <h4>{ post.metadata.title }</h4></Link>
                  <small>{ post.metadata.subtitle }</small>
                </div>
              </div>
              <div className="panel-body">
                <p>
                  { post.metadata.abstract }
                </p>
                <Link to={`/blog/${post.metadata.id}`}> <h4>{tr('Continue')}...</h4></Link>
              </div>
            </div>
          )}
        </div>
        <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12 blog-2-col">
          { Object.values(this.props.posts).sort((a, b) => { return b.metadata.id.localeCompare(a.metadata.id) }).filter((el, i) => { return i % 2 === 1; }).map((post) =>
            <div key={post.metadata.id} className="hpanel blog-box">
              <div className="panel-heading">
                <div className="media clearfix">
                  <a className="pull-left">
                    <img src={ post.metadata.authors_avatar } alt="profile-picture" />
                  </a>
                  <div className="media-body">
                    <span className="post-authorship">
                      <small><span className="font-bold">{ tr(post.metadata.author) }</span> </small>
                      <br />
                      <small className="text-muted"><i className="fa fa-calendar"> </i> { post.metadata.date_created }</small>
                    </span>
                    <span className="pull-right">
                      <small><i className="fa fa-clock-o"> </i> {tr('Time to read')}: { post.metadata.time_to_read } min</small>
                      <br />
                      <small>{ post.metadata.categories.map((category, index) =>
                        <span key={category}>
                          <span>{ !!index && ' ' }</span>
                          <span className={`label label-${category}`}>{ tr(category) }</span>
                        </span>
                      )}
                      </small>
                      {/*<small><i className="fa fa-eye"> </i> : 62 </small>*/}
                      {/*<br />*/}
                      {/*<small><i className="fa fa-comments-o"> </i> : 10</small>*/}
                    </span>
                  </div>
                </div>
              </div>
              <div className="panel-image">
                <img className="img-responsive center-block" src={ post.metadata.cover } alt="" />
                <div className="title">
                  <Link to={`/blog/${post.metadata.id}`}> <h4>{ post.metadata.title }</h4></Link>
                  <small>{ post.metadata.subtitle }</small>
                </div>
              </div>
              <div className="panel-body">
                <p>
                  { post.metadata.abstract }
                </p>
                <Link to={`/blog/${post.metadata.id}`}> <h4>{tr('Continue')}...</h4></Link>
              </div>
            </div>
          )}
        </div>
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12 blog-1-col">
          { Object.values(this.props.posts).sort((a, b) => { return b.metadata.id.localeCompare(a.metadata.id) }).map((post) =>
            <div key={post.metadata.id} className="hpanel blog-box">
              <div className="panel-heading">
                <div className="media clearfix">
                  <a className="pull-left">
                    <img src={ post.metadata.authors_avatar } alt="profile-picture" />
                  </a>
                  <div className="media-body">
                    <span className="post-authorship">
                      <small><span className="font-bold">{ tr(post.metadata.author) }</span> </small>
                      <br />
                      <small className="text-muted"><i className="fa fa-calendar"> </i> { post.metadata.date_created }</small>
                    </span>
                    <span className="pull-right">
                      <small><i className="fa fa-clock-o"> </i> {tr('Time to read')}: { post.metadata.time_to_read } min</small>
                      <br />
                      <small>{ post.metadata.categories.map((category, index) =>
                        <span key={category}>
                          <span>{ !!index && ' ' }</span>
                          <span className={`label label-${category}`}>{ tr(category) }</span>
                        </span>
                      )}
                      </small>
                      {/*<small><i className="fa fa-eye"> </i> : 62 </small>*/}
                      {/*<br />*/}
                      {/*<small><i className="fa fa-comments-o"> </i> : 10</small>*/}
                    </span>
                  </div>
                </div>
              </div>
              <div className="panel-image">
                <img className="img-responsive center-block" src={ post.metadata.cover } alt="" />
                <div className="title">
                  <Link to={`/blog/${post.metadata.id}`}> <h4>{ post.metadata.title }</h4></Link>
                  <small>{ post.metadata.subtitle }</small>
                </div>
              </div>
              <div className="panel-body">
                <p>
                  { post.metadata.abstract }
                </p>
                <Link to={`/blog/${post.metadata.id}`}> <h4>{tr('Continue')}...</h4></Link>
              </div>
            </div>
          )}
        </div>
      </div>
    )
  }
}

export default Blog



